
.shopcart {
  position: fixed;
  left: 0;
  bottom: 0;
  z-index: 50;
  width: 100%;
  height: 48px;
}
.shopcart .content {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    height: 100%;
    background: #141d27;
    font-size: 0;
    color: rgba(255, 255, 255, 0.4);
}
.shopcart .content .content-left {
      -webkit-box-flex: 1;
          -ms-flex: 1;
              flex: 1;
}
.shopcart .content .content-left .logo-wrapper {
        display: inline-block;
        vertical-align: top;
        position: relative;
        top: -10px;
        margin: 0 12px;
        padding: 6px;
        width: 56px;
        height: 56px;
        box-sizing: border-box;
        border-radius: 50%;
        background: #141d27;
}
.shopcart .content .content-left .logo-wrapper .logo {
          width: 100%;
          height: 100%;
          border-radius: 50%;
          text-align: center;
          background: #2b343c;
}
.shopcart .content .content-left .logo-wrapper .logo .icon-shopping_cart {
            line-height: 44px;
            font-size: 24px;
            color: #80858a;
}
.shopcart .content .content-left .logo-wrapper .logo.highlight {
            background: #00a0dc;
}
.shopcart .content .content-left .logo-wrapper .logo.highlight .icon-shopping_cart {
              color: #fff;
}
.shopcart .content .content-left .logo-wrapper .num {
          position: absolute;
          top: 0;
          right: 0;
          width: 24px;
          height: 16px;
          line-height: 16px;
          text-align: center;
          border-radius: 16px;
          font-size: 9px;
          font-weight: 700;
          color: #fff;
          background: #f01414;
          box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.4);
}
.shopcart .content .content-left .price {
        display: inline-block;
        vertical-align: top;
        margin-top: 12px;
        line-height: 24px;
        padding-right: 12px;
        box-sizing: border-box;
        border-right: 1px solid rgba(255, 255, 255, 0.1);
        font-size: 16px;
        font-weight: 700;
}
.shopcart .content .content-left .price.highlight {
          color: #fff;
}
.shopcart .content .content-left .desc {
        display: inline-block;
        vertical-align: top;
        margin: 12px 0 0 12px;
        line-height: 24px;
        font-size: 10px;
}
.shopcart .content .content-right {
      -webkit-box-flex: 0;
          -ms-flex: 0 0 105px;
              flex: 0 0 105px;
      width: 105px;
}
.shopcart .content .content-right .pay {
        height: 48px;
        line-height: 48px;
        text-align: center;
        font-size: 12px;
        font-weight: 700;
}
.shopcart .content .content-right .pay.not-enough {
          background: #2b333b;
}
.shopcart .content .content-right .pay.enough {
          background: #00b43c;
          color: #fff;
}
.shopcart .ball-container .ball {
    position: fixed;
    left: 32px;
    bottom: 22px;
    z-index: 200;
}
.shopcart .ball-container .ball .inner {
      width: 16px;
      height: 16px;
      border-radius: 50%;
      background: #00a0dc;
}
.shopcart .ball-container .ball.drop-enter-active {
      transition: all 0.4s cubic-bezier(0.49, -0.29, 0.75, 0.41);
}
.shopcart .ball-container .ball.drop-enter-active .inner {
        transition: all 0.4s linear;
}



