
.tab {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  height: 40px;
  line-height: 40px;
}
.tab .tab-item {
    -webkit-box-flex: 1;
        -ms-flex: 1;
            flex: 1;
    text-align: center;
}
.tab .tab-item a {
      display: block;
      font-size: 14px;
      color: #4d555d;
}
.tab .tab-item a.active {
        color: #f01414;
}



